<template>
    <div>

        <van-nav-bar title="专利创新生涯" left-text="返回" left-arrow @click-left="back">
            <van-icon name="search" slot="right"/>
        </van-nav-bar>
        <demo-block :title="123">

        </demo-block>
        <van-panel>
            <div slot="header">
                <van-cell :title="name" :label="org" class="author-name"/>
            </div>
            <van-cell-group>
                <van-cell icon="award-o">
                    <template slot="title">
                        <span class="custom-text">人才画像</span>
                        <van-tag plain type="danger" class="ttaagg" v-show="gyx">影响力人才</van-tag>
                        <van-tag plain type="primary" class="ttaagg" v-show="gcc">高产出人才</van-tag>
                        <van-tag plain type="success" class="ttaagg" v-show="gby">高被引人才</van-tag>
                    </template>
                </van-cell>

                <van-cell :value="a10.toFixed(2)">
                    <template slot="title">
                        <span class="custom-text">人才评分</span>
                        <van-rate v-model="value" disabled-color="#ffd21e" disabled/>
                    </template>
                </van-cell>

                <van-cell title="研究主题" :label="keywords"/>

                <van-collapse v-model="activeNames">
                    <van-collapse-item title="" name="1" icon="orders-o">
                        <div slot="title">详细指标</div>
                        <van-cell-group>
                            <van-cell title="专利总数" :value="a0"/>
                            <van-cell title="总被引次数" :value="a1"/>
                            <van-cell title="专利H指数" :value="a6" label="综合反映专利数量和质量"/>
                            <van-cell title="杰出专利数量" :value="a3" label="总被引次数前1%的专利个数"/>
                            <van-cell title="优秀专利数量" :value="a4" label="总被引次数前10%的专利个数"/>
                            <van-cell title="近2年产出专利加速度" :value="a9"/>
                            <van-cell title="年平均专利产出个数" :value="a8.toFixed(2)"/>
                            <van-cell title="科研时长" :value="a7" label="从第一次发表专利至今的时长"/>
                        </van-cell-group>
                    </van-collapse-item>

                    <van-collapse-item title="" name="2" icon="description">
                        <div slot="title">所有专利</div>
                        <van-cell-group v-for="(patent,i) in all_patents" @click="method_1(i)">
                            <van-cell :title="patent" is-link to="yingxiangli"/>
                        </van-cell-group>
                    </van-collapse-item>
                </van-collapse>

            </van-cell-group>

        </van-panel>

    </div>
</template>

<script>
    import {
        Checkbox,
        CheckboxGroup,
        Card,
        SubmitBar,
        Toast,
        NavBar,
        Tag,
        Pagination,
        Panel,
        Cell,
        CellGroup,
        Button,
        Progress,
        Collapse,
        CollapseItem,
        Rate

    } from 'vant';
    import axios from 'axios';

    export default {
        components: {
            [Card.name]: Card,
            [Checkbox.name]: Checkbox,
            [SubmitBar.name]: SubmitBar,
            [CheckboxGroup.name]: CheckboxGroup,
            [NavBar.name]: NavBar,
            [Tag.name]: Tag,
            [Pagination.name]: Pagination,
            [Panel.name]: Panel,
            [Cell.name]: Cell,
            [CellGroup.name]: CellGroup,
            [Button.name]: Button,
            [Progress.name]: Progress,
            [Collapse.name]: Collapse,
            [CollapseItem.name]: CollapseItem,
            [Rate.name]: Rate,
        },

        data() {
            return {
                activeNames: ['-1'],
            };
        },

        computed: {
            submitBarText() {
                const count = this.checkedGoods.length;
                return '结算' + (count ? `(${count})` : '');
            },

            totalPrice() {
                return this.goods.reduce((total, item) => total + (this.checkedGoods.indexOf(item.id) !== -1 ? item.price : 0), 0);
            }
        },

        methods: {
            method_1(i) {
                this.$router.push('/zhuanlidetail/' + this.all_patents[i] + '/' + this.tag);
            },


            formatPrice(price) {
                return (price / 100).toFixed(2);
            },

            onInit() {
                this.tag = this.$route.params.name;

                if (this.tag.indexOf('|') == -1) {
                    return;
                }
                this.name = this.tag.split('|')[0];

                const path = `api/person/` + this.tag;
                axios.get(path)
                    .then(response => {
                        this.org = response.data.org
                        this.a0 = response.data.a0
                        this.a1 = response.data.a1
                        this.a2 = response.data.a2
                        this.a3 = response.data.a3
                        this.a4 = response.data.a4
                        this.a5 = response.data.a5
                        this.a6 = response.data.a6
                        this.a7 = response.data.a7
                        this.a8 = response.data.a8
                        this.a9 = response.data.a9
                        this.a10 = response.data.a10

                        this.all_patents = response.data.all_patents

                        this.keywords = response.data.keywords

                        this.value = this.a10 / 20.0
                        console.log(response.data)

                        if (this.a0 >= 7) {
                            this.gcc = true
                        } else {
                            this.gcc = false
                        }

                        if (this.a1 >= 2) {
                            this.gby = true
                        } else {
                            this.gby = false
                        }

                        if (this.a6 >= 2) {
                            this.gyx = true
                        } else {
                            this.gyx = false
                        }

                        this.$forceUpdate()
                    })
                    .catch(error => {
                        console.log(error)
                    })

                this.$nexttick(() => {
                    window.scrollTo(0, 1)
                    window.scrollTo(0, 0)
                })
            },

            back() {
                this.$router.go(-1);
            }
        },

        created() {
            this.onInit();
        },
    };
</script>
<style lang="less">
    .author-name {
        font-size: 24px;
    }

    .ttaagg {
        margin-left: 5px;
    }
</style>